	module.exports = {"date":"2010-11-12 16:54:10","layout":"post","slug":"99-problems-but-crowdsourcing-aint-one","status":"publish","title":"99 Problems but Crowdsourcing Ain’t One","wordpress_id":"53","published":false,"body":"<p>Ahh, crowdsourcing. Every internet-connected designer’s favourite scapegoat; I don’t see it as a threat. Why?</p>\n<h2>What is crowdsourcing, and why is the devil incarnate?</h2>\n<p>In the event that my Mum reads this article (Hi Mum), Wikipedia defines crowdsourcing as</p>\n<blockquote>\n<p>The act of outsourcing tasks, traditionally performed by an employee\nor contractor, to a large group of people or community (a crowd),\nthrough an open call.</p>\n</blockquote>\n<h2>What’s the problem?</h2>\n<p>Designers think crowdsourcing is evil because it turns design into a commodity rather than a skilled profession, because it fills the world with mediocre design, and because everyone feels the need to moan about change.</p>\n<h2>Why don’t I care?</h2>\n<p>The thing is, there has always been a market for rubbish design.</p>\n<p><img src=\"http://img.skitch.com/20101112-frxb2encs9wdg1au2jqd485x5i.png\" alt=\"Commoditizing design? Design is a commodity. Great design isn't, but design is. Like tomatoes vs. heirloom tomatoes\"></p>\n<p>Shock horror but Web 2.0 didn’t commoditise graphic design. It exposed us to a slew of commoditised design, sure, but before the internet designers didn’t stand outside print shops watching clients pay for crap design. We’re simply more aware of the crap now because 99designs et al exist in the same sphere of the internet as the sites we frequent and so it is easier to get riled up about it. Designers are masochists.</p>\n<p>99Designs et al exist in the same sphere of the internet as our frequented blogs do so we tend to look at them. That’s all. We’re just exposed to them.</p>\n<p>No one is ruining design, no one is siphoning your work to India. If you previously provided cheap, shitty design then I’d be scared, because <em>that</em> is the design that is going to the lowest bidder. And rightly so - cheap, shitty design is cheap and shitty.</p>\n<p>I’d hope my colleagues and friends put a bit more effort into our work than that. We still educate our clients about the value of design in the hope that they realise the error of wanting a logo for £20 and a pint, but you can only lead a horse to water.</p>\n<p>The flip side of this is that <strong>there has always been spec work</strong>, and in some parts of the industry it is <strong>still accepted today</strong>. But I’ll wait until I run a multinational ad agency with the overheads to cover free pitching to worry about that.</p>\n<p>For now I’m going to focus on trying to do the best work I’m capable of!</p>\n"}

