	module.exports = {"date":"2011-08-01 10:09:01","layout":"post","slug":"living-the-dream-part-one","status":"publish","published":false,"title":"Living the Dream, Part One","wordpress_id":"66","categories":["Prismatic","San Francisco"],"body":"<p>If you’ve been following my tweets recently you’ll have noticed I’ve been waking up and going to sleep rather late. 8 hours late, actually. Why? I’m in San Francisco.</p>\n<p><img src=\"http://farm7.static.flickr.com/6143/5971280192_e2f6acef4d.jpg\" alt=\"bike+bridge 3\"></p>\n<h3>Why are you in San Francisco, Jon?</h3>\n<p>I’m making some awesome pixels with the guys at <a href=\"http://getprismatic.com\">Prismatic</a>. We haven’t nailed our elevator pitch yet (and I want to do the product justice), so I’ll just say it will involve an app in the sphere of content curation/sharing/consumption. Kind of.</p>\n<p>I’m working with some insanely talented engineers, and doing the most challenging IxD of my career so far; let’s just say it won’t be your run-of-the-mill RSS Reader. Stay in touch on <a href=\"http://dribbble.com/gold\">Dribbble</a> and <a href=\"https://twitter.com/jongold\">Twitter</a> to be the first to see and hear what we’re getting up to.</p>\n<p>Here’s the splash page I put up whilst we focus on actually building the product. We’re not looking for signups or anything right now (hence the lack of content), but everyone’s been asking to see what I’ve been working on so…</p>\n<p><a href=\"http://dribbble.com/shots/222908-Team-page-hover\"><img src=\"http://dribbble.s3.amazonaws.com/users/4046/screenshots/222908/please_allow_us_to_introduce_ourselves___prismatic.png\" alt=\"\"></a></p>\n<h3>How does San Francisco compare to London?</h3>\n<p>I’ve lived in London for 22 years, I love it more than anything in the world and I will always be a Londoner at heart. But this place is <em>next level</em>. I don’t want to descend into clichés but there’s an awesome buzz here.</p>\n<ul>\n<li>I walked past Jack Dorsey in the street on the day I arrived.</li>\n</ul>\n<ul>\n<li>I’ve met loads of amazing people at great startups (there are startup events <em>every night of the week</em> here).</li>\n</ul>\n<p>Maybe I didn’t make enough of an effort in London; maybe I socialised too much with the design scene and not enough with the startup scene (they’re relatively fragmented in London); in any case I see San Francisco as a massive boost to my career — and I think it should be to yours too.</p>\n<h3>My career? Really?</h3>\n<p>Sure, move out here. If nothing else it will give you an amazing experience to take back to London, Lisbon, Lagos, or whereever you currently work.</p>\n<h3>Jump</h3>\n<p>A few years ago a classmate at uni who’d worked at a big agency in New York mentioned I should stay in London, that the American design scene is too much of a culture shock to be productive. I now realise that that’s exactly why you <em>should</em> throw yourself into new experiences. He said that in New York people worked till 8pm(!). It’s 3am on a Sunday night and we’ve just finished work. Suck it.</p>\n<p>Maybe it’s a startup thing - that if you have some vague notion that you’re trying to make the world a better place it becomes fun; if you’re rebranding a nothingy corporate client you want to end your life a minute past 5pm. But anyway.</p>\n<h3>Come to California, build awesome stuff.</h3>\n<p>Everyone is looking for designers here. <a href=\"http://www.quora.com/Designers/Why-is-there-such-a-stunningly-short-supply-of-designers-in-Silicon-Valley-right-now\">Here’s a huge thread on Quora</a> talking about the shortage of designers. If you have a portfolio, send it to me and I can pass it on. If you want advice, email me. Seeing <a href=\"http://blog.lukesbeard.com/archive/2011/1\">Luke Beard’s blog</a> about working at <a href=\"http://www.zerply.com/\">Zerply</a> earlier this year really inspired me to find work in America after dreaming about it for years; if I can return the favour and help another designer make that leap of faith it would be awesome.</p>\n<h3>If in doubt</h3>\n<p>Say you’re creating ‘the Airbnb for geosocial hyperlocal Instagram-esque vertically-integrated metastartups’. I don’t know what that means but presumably someone in the Valley does.</p>\n"}

