	module.exports = {"date":"2011-08-03 04:00:07","layout":"post","slug":"thinking-about-design-school","published":false,"title":"Thinking about Design School","wordpress_id":"41","categories":["Design Education","Ravensbourne"],"body":"<p>One of the articles I’ve been meaning to write for a while is a\nsummation of my thoughts on Design School, and its relevancy in 2011.\nBy ‘a while’ I mean at least the 3 years I was in university education\nfor; I also did a UK college Graphic Design course (the equivalent of US\nCommunity College?) for two years before that.</p>\n<p><strong>So I’ve been in, around, and thinking about Design Education for a\nlong time</strong>; I’m just really shit at finishing blog posts. But I have a\nshiny new blog urging me to write on. So here we go, stream of\nconciousness style.</p>\n<blockquote>\n<p>Before I start, it’s been difficult over the years to restrain public\nranting about Ravensbourne. What follows is a completely objective\npost about my experience there, after the dust has had sufficient time\nto settle; it’s more rational and less emotional than the drafts I’ve\nwritten over the years.</p>\n</blockquote>\n<h3>The Good.</h3>\n<p>Learning typography in a traditional setting, from people who have been\nthere (Basel/Zurich), done that, and kerned the t-shirt. Typesetting <em>by\nhand, on paper</em>. Learning about grids, 1-on-1, from actual Modernists\nwho actually knew Muller-Brockmann. <strong>Having Wim fucking Crouwel come in\nfor an intimate lecture on your birthday</strong>.</p>\n<p>Spending three years in a studio with classmates— laughing, joking,\nkerning, stressing out, sticking in, cutting out, learning together\nand <strong>teaching each other</strong>. Having older students to look up to, and\nhaving younger students to (hopefully) pass things on to. <strong>Just being\nin that environment for that amount of time is incredibly valuable.</strong></p>\n<p><strong>You can’t replicate that learning from home</strong> (due to the internet not\nbeing the greatest setting for learning design), or at an\ninternship/apprenticeship/etc (due to time constraints), but I’m not\nsure it’s valuable enough to justify the investment.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/07/n725840617_5477295_151.jpg\" alt=\"\"></p>\n<h3>The Bad.</h3>\n<p><strong>Feeling like The Good Ship Web Design is sailing off without you_.\n_<strong>Thinking you’ve had enough learning and want to start doing. Right\nnow.</strong> Before another amazing site gets launched that you didn’t have\nthe chance to work on.</strong> Because you’re sat in a classroom meeting\nunproductive assessment criteria when you could be doing meaningful\nwork. Seeing younger designers (who haven’t committed to years of\neducation) achieve great things, and curse yourself for being stuck in\nthis situation.</p>\n<p>**On Sketchbooks, and ticking boxes: **of course we have to learn to\ndesign on paper. No one disputes that. Going straight to the Mac is a\nmistake we’re all guilty of in the early stages of our careers, and we\ncould all stand to keep better visual scrapbooks. <strong>Sketchbooks are\nbrilliant.</strong></p>\n<p><strong>But not when they’re a key component of assessment</strong>. I’m dyspraxic\nand, to cut to the chase, when I’m in <strong>the zone</strong> I can’t slow down,\nprint out what I’m doing, neatly cut it out and stick it into a\nsketchbook, then annotate it just to show the assessors my thought\nprocess. Not without ruining my work and focus.</p>\n<p>Just one example.</p>\n<p>Outdated assessment styles and disability discrimination combining to\nmake for <strong>mediocre outcomes</strong>; just one example of how inappropriate\nand self-serving design education has become. Nice. **If a degree has to\nbe compromised to be shoehorned in to the national education framework\nthen it’s not appropriate anymore. **</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/07/n725840617_5477285_8646.jpg\" alt=\"\"></p>\n<h3>Education can’t do digital.</h3>\n<p>The problem is BA (Hons) degrees need to be validated. They need to have\na set curriculum, with set projects and assessment criteria for those\nprojects. Web design is moving so fast that just taking an hour out to\nwrite this post has caused me to fall behind(!). <strong>There’s no way that\nsomething as fast moving as web design can be shoehorned into a 3-year\nvalidation cycle.</strong> Ish.</p>\n<p><strong>Front-end code can’t be taught in universities.</strong> Period. It’s moving\ntoo fast. C++ and Java can be taught, but they’re not moving at\nbreakneck pace. <strong>Let’s forget the idea of web design education\nrequiring a code component.</strong> I just can’t see a way it can be done. <strong>I\nstill passionately believe that every graduating designer should know\nhow to write HTML and CSS, but the classroom isn’t the place for it.</strong></p>\n<p>What the classroom <strong>can</strong> teach is design for the web. User Experience\ndesign. <strong>How to think like a web designer, rather than a print designer\ndoing a website</strong>. Big difference. And that’s not being taught properly\nbecause the staff aren’t in place. With such a high demand for good web\ndesigners at the moment who would choose to teach? What you get is\ntutors who honed their skills as print designers being told by\nmanagement to ‘make things digital’. It doesn’t work.</p>\n<p>But that’s the only hope I can see for design education - <strong>have\nrelevant tutors teaching relevant design, or die</strong>.</p>\n<h3>The Outcome.</h3>\n<p>Things worked out for me in the end, but I wonder if it was directly a\nresult of design education as a system, or a combination of chances on\nthe side. One example is if Ravensbourne hadn’t scrapped its analog\nfacilities (screenprinting, letterpress, darkroom) I would have been\ncontent making posters. Without these facilities I became jaded with\nprint design which led me to web design. <strong>In a roundabout,\nunintentional way, my education made me a web designer.</strong></p>\n<p>But that’s just my story - knowing what I know now, I don’t think I\ncould recommend any design course apart from <a href=\"http://www.hyperisland.se/\">Hyper\nIsland</a>. Possibly some of the <a href=\"http://www.arts.ac.uk/\">UAL\ncolleges</a> if you’re intent on being a print\ndesigner at the end of it but** so was I**.</p>\n<h3>‘But I want to be a print designer!’</h3>\n<p>In 3 years I’m sure the web will be even a more attractive prospect than\nit is today, and I think you’d be foolish to not pursue a career in\ndigital design.** Seriously. It’s awesome.**</p>\n<h3>What are the other options?</h3>\n<p>Try to find a job somewhere that will teach you the ropes. Not an agency\nstill creating Flash or tables. Apprenticeships like <a href=\"http://www.markboulton.co.uk/journal/comments/want-to-be-our-apprentice\">Mark\nBoulton</a>\nwas offering are a great idea. I don’t have the answers yet, but I do\nknow that design school shouldn’t be the best one. I really hope some\nday we can solve this problem, because there <strong>are</strong> positives to formal\ndesign education. I’m just incredibley jaded by my experiences.</p>\n"}

