	module.exports = {"date":"2011-10-26 18:13:56","layout":"post","slug":"burgers-tablets-typography","status":"publish","title":"Burgers, Tablets & Typography","published":true,"body":"<p>If there’s one thing I love more than talking about technology and looking at typefaces, it’s burgers. My stomach was pleasantly surprised to discover In-N-Out Burger whilst living in San Francisco this summer but something else struck me about visiting the restaurant.</p>\n<p>They’re a great case study in contrast. Contrast is <em>the</em> differentiating factor in turning otherwise good work to great - from typography and grid systems to whole product lines. I’ll get back to the design and tech in a minute, but first, burgers.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/innoutsign.png\" alt=\"\"></p>\n<h3>Burgers and Choice Paralysis</h3>\n<p>For those who haven’t visited, In-N-Out Burger sell three burgers - the hamburger, the cheeseburger, and the double cheeseburger. No vegetarian burger, no chicken burger, no fish burger. No Texas BBQ burger, no Angus Bacon Double Whopper Extreme burger.</p>\n<p>Just a burger, a burger with cheese, and a burger with cheese and more meat. Easy to pick, easy for the franchise to make, easy product line to maintain. Better by less.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/innoutmenu.png\" alt=\"\"></p>\n<p>Let’s compare that to <a href=\"http://www.mcdonalds.com/us/en/full_menu_explorer.html\">McDonalds’ menu</a>. I count 57 burgers, main salads and wraps; that’s before we get to sides and desserts.</p>\n<p><a href=\"http://www.mcdonalds.com/us/en/full_menu_explorer.html\"><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/mcdonaldsmenu.png\" alt=\"\"></a></p>\n<p>Perhaps three burgers is <em>too spartan</em> a menu. That’s fine - I’m partial to some chili and guacamole on my burgers from time to time, and vegetarian friends felt alienated when the rest of the group suggested eating at In-N-Out. But that doesn’t mean that we should go back to the McDonalds option of having several dozen shitty products instead of three great ones though. Five options? Six? Seven? Some kind of reasonable compromise before descending into fast food choice paralysis hell.</p>\n<p>Of course I’m not suggesting that we make a habit of eating fast food (or offering business advice to McDonalds), so let’s look further afield. Where else do we have no product differentiation and ridiculous naming strategies? Technology, of course!</p>\n<h3>The elephant in the phone store</h3>\n<p>If I was going to buy an Android phone I’d have a hard time making up my mind. “A Galaxy? A Nexus? A Nexus S? A Galaxy Nexus Droid Extreme SII Ace? Wait, a Galaxy Nexus is a thing? I thought they were separate products? Gah, sod it, I’ll buy an iPhone”</p>\n<p>This choice isn’t empowering the consumer; it’s not being kind and giving them options. It’s two things - overpowering them with choice to scare them into buying something, and releasing a string of mediocre, expendable and replaceable products. Not cool.</p>\n<p>Apple sell the new iPhone, the slightly older iPhone, and the old-but-still-better-than-any-Android iPhone. The three hamburgers.</p>\n<p><a href=\"http://www.apple.com/iphone/compare-iphones/\"><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/Apple-iPhone-Compare.png\" alt=\"\"></a></p>\n<h3>Size matters</h3>\n<p>Why won’t Apple release a 4&quot; iPhone and a 7&quot; iPad? The In-N-Out Principle at work again. A 7&quot; iPad would dilute the value of the 9.7&quot; iPad. Different sizes suggest different use cases - a 7&quot; iPad wouldn’t be different enough to the 9.7&quot; iPad to warrant different applications, context and mindset. It would just be a slightly squised 9.7&quot; iPad.</p>\n<p>Samsung have released or announced 5.4&quot;, 7&quot;, 7.7&quot;, 8.9&quot; &amp; 10.1&quot; tablets (with multiple models of some); in addition to a range of phones which goes up to the 4.65&quot; Galaxy Nexus. With such small gaps between products, Samsung has ruled out the possibility of unique and innovative uses. Fast food technology.</p>\n<p><a href=\"http://c.jongold.in/0M1A1A0d3R0f100b031D\"><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/samsungmobilecomparison.png\" alt=\"\"></a></p>\n<p>For more on choice paralysis and watered-down product lines, <a href=\"http://arstechnica.com/hardware/news/2011/09/ultrabook-intels-300-million-plan-to-beat-apple-at-its-own-game.ars/2\">Peter Bright’s article on Ars Technica about Ultrabooks</a> nails it - again compare Dell, HP or Toshiba’s lineups to Apple’s computers…wait, haven’t I seen this somewhere before?</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/grid.jpeg\" alt=\"\"></p>\n<p>There it is.</p>\n<p>iMac, PowerMac, iBook, PowerBook.</p>\n<p>Hamburger, Cheeseburger, Double Cheeseburger.</p>\n<p>Cut the shit - it increases the value of the sum of the parts.</p>\n<h3>I’m bored of reading about Android and PCs, Jon</h3>\n<p>Good, I’m bored of writing about them! Let’s go back to graphic design. I’ve been meaning to write an article about the principles behind good grid systems for a while now.</p>\n<p>In the same way as fewer burgers to choose from helps us make a decision and fill our stomach with meaty goodness, fewer possible combinations of grid units make each one stronger.</p>\n<p>The simplified layouts below take the same basic shape  - using a more rigid grid with less flexibility makes for a more tense layout; increasing options makes the whole thing more sloppy.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/gridgrey-01.png\" alt=\"\"></p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/gridgrey-02.png\" alt=\"\"></p>\n<p>And an example with more representative content - again the beauty of a grid (unity, contrast and tension) is lost with an increasing number of decreasingly significant options for content placement.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/gridgrey-04.png\" alt=\"\"></p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/gridgrey-03.png\" alt=\"\"></p>\n<p>Again, I’m not suggesting that we should always stick to 4 column grids, but when you start your next project make sure you consider the implications and relevance of the grid you’re working with. Less is often more.</p>\n<h3>Surely you can’t make typography about burgers too?</h3>\n<p>You bet! This is the big one. I’ve been <a href=\"http://designedbygold.com/2011/10/calendar/\">offering little tidbits of design advice</a> to early-stage startups and aspiring designers recently, and one thing that keeps coming up is refining typography. Not a huge overhaul, not the Swiss ‘kerning tiny bits of paper by hand’ exercises we were forced to do in design school (more on that another time) - just little tweaks and principles that make a world of difference. How? Limiting options, of course!</p>\n<p>Creating and sticking to a strict type scale is a great first step. Limit the options of typefaces and weights. One typeface in one weight in one size can be effective; two weights, typefaces and sizes is fine. But using 5 typefaces on a site, in an inconsistent range of sizes and every weight they come in? You’re back to McDonalds again.</p>\n<p>In Josef Müller-Brockmann’s seminal book ‘<a href=\"http://www.amazon.co.uk/Grid-Systems-Graphic-Design-Typographers/dp/3721201450/ref=sr_1_5?ie=UTF8&amp;qid=1319646813&amp;sr=8-5\">Grid Systems in Graphic Design</a>’ there’s a brilliant illustration of this concept - if you don’t own the book then I highly recommend it.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2011/10/Grid-Systems-1.png\" alt=\"\"></p>\n<p>Of course this principle isn’t just applicable to graphic design, tablet computers and beefburgers - in almost any field or application, limiting choice makes each option stronger by itself and stronger as the sum of parts.</p>\n<p>And next time you’re in California, stop by In-N-Out.</p>\n"}

