	module.exports = {"date":"2011-10-03 12:31:19","layout":"post","slug":"calendar","published":false,"title":"Opening up my calendar","wordpress_id":"167","body":"<p>I’ve freed up ~an hour/day in my calendar to help advise early-stage startups and young designers — <a href=\"http://officehours.tv/\">Office Hours</a> style. I chose the time slot 4pm GMT to be convenient for a range of time zones - that’s 8am in SF and 11am in NYC.</p>\n<p>I’ll see how it goes and tweak as necessary, but for now if you’re a pre-money startup, or a (prospective?) design school student feel free to RSVP to one of these slots with your Skype or iChat username and we’ll go from there!</p>\n<p><a href=\"http://c.jon.gd/2k1O2J3M2I1P3J2s3V1h\">My availability on Google Calendar</a></p>\n"}

