	module.exports = {"layout":"post","slug":"why-i-code","published":false,"date":"2011-12-21 01:29:29","title":"Desingineering: Why I Code","body":"<blockquote>\n<p>“Desingineer” -&gt; mythical person startups are looking for who can do UI, UX and also excellent front- and back-end coding.\n— chris dixon (@cdixon) <a href=\"https://twitter.com/cdixon/status/148896542462455808\">December 19, 2011</a></p>\n</blockquote>\n<p>You don’t need to code.</p>\n<p>If you’re a talented designer you probably don’t need to learn Ruby on Rails. JavaScript is something your front-end engineer writes. Touching Objective-C and the iOS SDK is outside of your job description, surely. You don’t even need to be able to write the most semantic HTML or the most cutting edge CSS — a designer’s job is to design. <em>Kind of.</em></p>\n<p>But then you could get away without manually kerning, or devising a typographic scale, or putting your type on a baseline grid, or trapping your artwork for print, or making every single pixel perfect.</p>\n<p>The point is, just because you can cut corners doesn’t mean you should. Sweating the details is what makes the best designers, and similarly the most curious designers are the most employable.</p>\n<p>No, you won’t be working for Google designing interfaces AND writing production-ready, scalable backend stuff, but the chances are that being curious about this stuff makes you more open to opportunities, more able to patch a hole in an emergency, and a more rounded designer all over.</p>\n<p>If you care about every pixel then I think it’s irresponsible to not want to understand every other layer of the stack.</p>\n<p>Spreading yourself too thin is a legitimate concern - no one wants to be a mediocre designer and a mediocre coder. I don’t think this is a predetermined fate though - I’d rather occasionally do a bit less design than I like and keep up to speed with technology than fall behind and be_ that 30-something Art Director who is afraid of anything other than FreeHand and OS9_.</p>\n<p>—</p>\n<p>Me? I could never write a JavaScript game but I can do all the jQuery I need to know on a day-to-day basis. AJAX calls and interactiony things. I can write simple apps in Rails &amp; Django - just enough that I can get out a working prototype of <em>hotnewstartup</em> without relying on others. And I’m a crappy iOS developer, but I’d like to think I’m better for knowing the basics.</p>\n<p>That’s me, how about you?</p>\n"}

