	module.exports = {"title":"Being a Dick & Challenging the Status Quo","layout":"post","date":"2012-01-03T00:00:00.000Z","published":true,"body":"<p>There was another little tiff on Twitter today.</p>\n<p>The names don’t matter; the song remains the same as ever.</p>\n<p>An experienced and well-respected designer wrote a blog post suggesting that a current school of thought is the One True Way (OTW), and anyone not practising OTW isn’t a real web designer. An up-and-coming young designer disputed that and called him a dick. Cue lots of people attacking and defending both sides.</p>\n<p>The thing is, nothing should be holy. OTW is great right now, and I have a great deal of respect for the industry figures pushing it and opening our eyes to it. But it’s just a school of thought. Quickly and routinely suppressing any mention of criticism to get more life out of your current conference talk? That’s pretty lame, and it’s against the spirit of innovation.</p>\n<p>Up and coming young designers should be questioning <em>every single thing</em> we hear at conferences, intelligently reasoning with every blog post and every hot new technique. If you come back at the end of it agreeing with the motion, great. If you disagree, also great. This industry is about figuring things out and innovating. Making up your own mind. Not getting preached to.</p>\n<p>But don’t be a dick, and don’t call people dicks. And if you’re that well-respected industry figure, you should know better than to threaten to break a 21-year-old’s fingers.</p>\n<iframe width=\"480\" height=\"360\" src=\"//www.youtube.com/embed/J7532GXPnO8?rel=0\" frameborder=\"0\" allowfullscreen></iframe>\n<p>There’s also been talk recently of less ‘snark’ on the internet recently. This isn’t what <a href=\"http://www.sazzy.co.uk/2011/12/do-unto-others-2/\">this</a> or <a href=\"http://www.cennydd.co.uk/2011/open-letter/\">this</a> were. Just to keep that clear. Constructive criticism isn’t always a personal attack, and without constructive criticism we won’t progress as an industry…</p>\n"}

