	module.exports = {"layout":"post","title":"Lean Startup Machine","date":"2012-10-09T00:00:00.000Z","body":"<p>Over the weekend I attended <a href=\"http://leanstartupmachine.com/\">Lean Startup Machine’s</a> London workshop. I’ve been <a href=\"http://steveblank.com/books-for-startups/\">reading</a> lots about Lean and Customer Development recently but have been stumbling at applying the theory. I couldn’t pass up the opportunity to spend a fast-paced weekend learning about it from amazing mentors!</p>\n<p><strong>What is Lean Startup Machine?</strong> Different to the type of hackathon you might be used to, Lean Startup Machine focuses on <strong>validated learning</strong> as an outcome rather than coded &amp; designed prototypes. It will take you from knowing nothing about Lean to being well versed in the theory and <strong>practice</strong> in 48 hours. Ryan and his team leave no one behind and emphasise the importance of understanding the process.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2012/10/ValidationBoard.jpg\" alt=\"Our ValidationBoard\"></p>\n<p>During the weekend, you’ll make friends with the <a href=\"http://leanstartupmachine.com/validationboard/\">Validation Board</a> - a great way to note down your ideas and keep track of progress. You might be familiar with the <a href=\"http://www.businessmodelgeneration.com/canvas\">Business Model Canvas</a> (a visual grid for describing and iterating on business models); the Validation Board is a similar idea for earlier stage exploration. It’s a really neat way to note down your Hypotheses and  Assumptions at each step of the process, and keep track of the work you’ve done. I found using the Validation Board invaluable in two areas - it helped crystallise our team’s thinking, and it allowed wandering mentors and friends to understand our progress quickly and easily. 5-7 words per post-it note, all uppercase, no rambling. Boom!</p>\n<p>You can <a href=\"http://leanstartupmachine.com/validationboard/\">download a Validation Board for free online</a> as a PDF and Keynote template, but consider taking it to a print shop and getting an A1 print to stick on your wall. Also take some time to watch the videos on the site - they go some way to explaining how it should be used.</p>\n<p><img src=\"%7B%7Bsite.root%7D%7D/public/images/p/2012/10/ValidationBoard.gif\" alt=\"Our ValidationBoard\"></p>\n<p>Before this weekend I wondered what people <strong>actually do</strong> at a Lean Startup Machine course - I knew there was no hacking or designing involved, but I couldn’t picture ‘talking to customers’. Would we be roleplaying? Would a mentor stand on stage and just tell us theoretical teachings? Hell no! <strong>The weekend revolved around getting out of the building</strong> (physically and metaphorically). ‘Get Out Of The Building’ is a synonym for ‘Getting Out of Your Head’ - it turned out phoning contacts and conducting online qualitative &amp; quantitative research was ‘out of the building’ too.</p>\n<p>But I digress - <strong>actually getting out of the building and talking to real strangers was hugely valuable to me.</strong> I definitely have a tendency to bottle up ideas and wait until they’re ‘perfect’ to unleash them on the world - no more!.</p>\n<p>Our initial idea was a ‘platform for sci-fi fans to get involved with the production of movies’. <strong>Kickstarter meets GitHub. In space.</strong> A way for fans to cut through blockbuster disappointment and get the movies <strong>they wanted</strong> produced. This worked out quite well for the exercise - sci-fi fans are a very targeted demographic and this allowed us to pin them down to some quite specific geographic locations. So first thing on Saturday morning we went to the ‘scene of the customer’ —Forbidden Planet in Soho—and talked to some customers!</p>\n<p>We learnt a few things that morning:</p>\n<ul>\n<li>\n<p>Speaking to strangers isn’t so scary once you get into it.</p>\n</li>\n<li>\n<p><a href=\"http://dex.io/robfitz/how-to-screw-up-custdev/\">Asking non-leading questions</a> is really difficult.</p>\n</li>\n<li>\n<p>The people we talked to hated our idea.</p>\n</li>\n<li>\n<p>Know when’s the time to pitch your ideas to people - you learn a lot more by <em>listening</em> rather than <em>talking</em>.</p>\n</li>\n</ul>\n<p>From this initial invalidation we picked up on some great insights from our customers - they were mainly happy with the selection of media available, but lots had friends producing low-budget horror—not sci-fi—films. Ketchup and a MiniDV camera, that sort of thing. These friends supposedly had a tough time getting their films heard about online, which we thought was a shame, so <strong>we decided to pull our first pivot and talk to some filmmakers.</strong> Surely they’d love our new idea of a social media marketing &amp; analytics tool?</p>\n<p>One of the things every team was worried about this was <strong>‘where do we find customers to talk to at the weekend?’</strong>. In the real world we have days and weeks to track down people to talk to, but we had to move ultra-fast and iterate several times over a weekend. Answer? You’ll figure it out, trust me.</p>\n<p>Scouring Facebook, Twitter &amp; LinkedIn, wracking our brains and breaking into a film school (seriously) we found ~20 filmmakers eager to talk to us about their craft. We were convinced they’d love our social media idea (though again we weren’t pitching it to them - rather seeing if they’d validate our assumptions). Again a resounding rejection - a vast majority of them pointed out that they had to keep their work <strong>offline</strong> so it was eligible for film festivals - the major events that they were worried about. <strong>Pivot again</strong>, this wasn’t working.</p>\n<p>Talking to more filmmakers we found out that whilst film festivals are important, after the festivals lots of films fail to find their way to an audience. Our final idea - <strong>a service to let fans watch independent films that otherwise wouldn’t be made available.</strong> A two-sided market, we also envisaged it as a <strong>platform for filmmakers to gain leverage and social proof to showcase their work to independent cinemas</strong> in the hopes of bringing them back to the silver screen. The filmmakers and film festival organisers we spoke to were unanimously keen on this idea (some even rushing to send us trailers and information to use) - which we took as validation. Running out of time in the weekend, we also decided to put up a landing page to gauge whether fans would sign up.</p>\n<p><a href=\"http://underdogfilms.co\">It’s not the prettiest site</a> I’ve hacked\ntogether but for an hour’s work (and treating it just as part of the\nprocess &amp; learning experience) we felt it did a great job - £75 of\nAdWords, a couple of tweets and several hundred visitors later it’s\nstill maintaining a <strong>17% conversion rate</strong>. Better than the 3% conversion rate we’d hoped for!</p>\n<p>Overall a fantastic weekend - I met some fantastic people (participants,\nmentors and the customers we spoke to!) and <strong>learnt more in 48 hours</strong> than I thought possible. There’s definitely more we could have done at every stage, but I’m glad we didn’t obsess on detail and instead moved quickly through the process - there’s time to conduct really thorough experiments in the real world!</p>\n<p>Top things I learnt this weekend</p>\n<ul>\n<li>\n<p>Don’t hold onto ideas too tightly</p>\n</li>\n<li>\n<p>(In)validate your assumptions</p>\n</li>\n<li>\n<p>Talk to real people, not your friends &amp; parents!</p>\n</li>\n</ul>\n<p>I had a fantastic time and would recommend it to anyone (the crowd was surprisingly diverse - not the usual homogeneity of designers &amp; developers). The next London workshop is taking place in <a href=\"http://leanstartupmachine.com/events/london-december-14-16/\">December</a> (and the team put on events all around the world) - I hope you get out of it as much as I did!</p>\n"}

