	module.exports = {"date":"2013-07-17","layout":"link","title":"Meet HireMyFriend","published":true,"header-img":"https://d262ilb51hltx0.cloudfront.net/proxy/0*7t0yjVF0Bu-YpmzJ.png","external_url":"https://medium.com/@jongold/meet-hiremyfriend-a-new-product-from-makeshift-372c64f3d833","body":"<p>Today we’re really excited to share our latest product with you —\n<a href=\"http://hiremyfriend.io/\">HireMyFriend</a>. You can check it out right now, but\nfirst I’d like to tell you a little about why we’ve made it.</p>\n<h3>#startuplife</h3>\n<p>A couple of months ago a friend came to me with a problem. The startup she\nworked at was imploding—as they do—and she needed to find a new job. She is a\nrespected designer with a whole bunch of Twitter followers and LinkedIn\nconnections that she could usually reach out to for leads, but in this case\ncouldn’t.</p>\n<p>When you’re in a full-time job and need to find new work, it’s really\ndifficult to reach out for help.</p>\n<blockquote>\n<p>“Hey guys, I’m looking for work as a visual designer. I’m London based and\ncan start from Monday”</p>\n</blockquote>\n<p><strong>If you tweeted that you’d get fired!</strong> You have to have some tact; unfortunately this usually just means silence. Sneakily emailing your confidants is risky and time consuming; even updating your LinkedIn profile looks suspicious. That network you can usually rely on for favours in a time of need? Gone.</p>\n<p>Over the next few months I noticed this recurring trend. <strong>My friends who work\nas freelancers would blissfully tweet out their availability</strong>—which I’m more\nthan happy to pass around—whilst my friends in full-time jobs tip-toed around\nand only revealed their career intentions in hushed whispers.</p>\n<p>There would be occasional private messages: <em>“Hey, do you know anyone hiring?\nPlease don’t tweet about it!”</em>, and public tweets saying <em>“My friend is\nlooking for work”</em>, but that seems like a slow &amp; disjointed way of finding\nopportunities.</p>\n<p><strong>The web is revolutionising the way we look for work</strong>—and as employers, the way we find talented people to work with—but lots of people are left out of this process.</p>\n<h3>A little help from our friends.</h3>\n<p>We want everyone to find a job they love and we want to avoid awkward\nsituations in the job search process. We want to give full-time employees the\nsame freedom to move around that freelancers enjoy. And we want to make job\nsearches social.</p>\n<p>At <a href=\"http://makeshift.io/\">Makeshift</a>, we started prototyping tools to make\nthis a thing of the past, which brings us to today.</p>\n<p>Introducing <a href=\"http://hiremyfriend.io/\">HireMyFriend</a>.</p>\n<p><strong>Here’s how it works:</strong></p>\n<p><img src=\"https://d262ilb51hltx0.cloudfront.net/max/800/0*kO6Ge2Ww7wzkvg5G.png\" alt=\"\"></p>\n<p><strong>Create a profile</strong>. It can be as vague or descriptive as you want. We’ll blur out your name and face!</p>\n<p>It’s totally anonymous.</p>\n<p><img src=\"https://d262ilb51hltx0.cloudfront.net/max/800/0*ZUelPFMk0uYZbILj.png\" alt=\"\"></p>\n<p><strong>Ask some friends for a helping hand</strong>. They’ll give you an endorsement and—if you ask nicely—share your profile for you.</p>\n<p>You know your friend who just wrote a book that everyone is talking about? Ask\nher. Or your friend who has a zillion Twitter followers? Ask him! <strong>Get people\ntalking!</strong></p>\n<p>Recommendations from the right people will get employers excited.</p>\n<p><img src=\"https://d262ilb51hltx0.cloudfront.net/max/800/0*efkA2cQPybrITLiv.png\" alt=\"\"></p>\n<p><strong>Employers get in touch</strong>. This is where you can drop your veil. We’ll filter out the sketchy recruitment agencies, and you can choose who finds out your true identity.</p>\n<p>Did your current boss coincidentally get in touch? It’s a small world, but\nwe’ve got you covered — just reject the message and she’ll be none the wiser.</p>\n<p>I’m really excited about <a href=\"http://hiremyfriend.io/\">HireMyFriend</a>. It has\nalready helped place people in great companies, and I’d love for you to try it\nout.</p>\n<p>Special thanks go to <a href=\"http://twitter.com/jasecoop\">Jase</a>,\n<a href=\"http://twitter.com/stef\">Stef</a>, <a href=\"http://twitter.com/cbm\">CBM</a>,\n<a href=\"http://twitter.com/choosenick\">Nick</a>, &amp; the rest of the Makeshift team for\nmaking this plan come together :)</p>\n"}

