	module.exports = {"date":"2014-04-28","layout":"post","slug":"moving-on-from-makeshift","title":"Moving on from Makeshift","header-img":"/public/images/p/2014/04/tumblr_n3ttaoFz9x1st5lhmo1_1280.jpg","published":true,"body":"<h2>:q</h2>\n<p>After a great year or so of relentless hacking, I’ve decided to move on from Makeshift. It’s been a good ride — I’ve made a <a href=\"http://hiremyfriend.io\">whole</a> <a href=\"http://attending.io\">load</a> <a href=\"http://helpmewrite.co\">of</a> <a href=\"http://listerly.co/\">awesome</a> <a href=\"https://dribbble.com/shots/1029671-Bitsy-Homepage?list=users&amp;offset=11\">products</a> (and in doing so registered most of the .io domains on the internet), met &amp; worked with amazing people, and grown immeasurably as a designer &amp; programmer.</p>\n<p>But all good things must come to an end. At Makeshift my involvement with new product prototyping has slowed down as we focus on growing our existing products; the team of generalist hackers slowly giving way to talented specialists. That’s the nature of startup teams, though. As much as I love Makeshift our paths have been diverging for a while and I think this is as good a point as any to disembark the startup strugglebus and ride off into the distance in the muscle car of doing-my-own-thing.</p>\n<h2>:enew</h2>\n<p>What’s the plan? At least in the short term I’ll be doing freelance design &amp; development - hopefully with small startups where I can add the most value.</p>\n<p>The timing couldn’t be better. In the past few months it seems like all of my talented friends have left their full-time jobs in search of the freedom freelancing brings. The London product design scene has become close knit over the past year and this is a great time to collaborate with people I’ve wanted to work with for ages. I suspect there will be lots of shared projects, knowledge, and most importantly, beers.</p>\n<p>I’ll also be setting aside time to build my own products. It’s been super gratifying seeing real people get real jobs through <a href=\"http://hiremyfriend.io\">Hire my Friend</a>; since we started building it last June I’ve had a bunch of other ideas I’ve been desperate to build but that I could neither do justice working on in the evenings nor fit into Makeshift’s product roadmap. That’s all going to change - there will be some exciting new bits of internet coming soon and I’m excited to share them with you.</p>\n<h2>Available for websites, apps, conferences, weddings &amp; bar mitzvahs.</h2>\n<p>Business time. You can hire me for design and front-end, Ruby, JavaScript &amp; iOS development. I’ve been doing lots of early-stage product design &amp; development recently—if you need help getting an MVP out the door, I could be your guy—but am equally up for doing new things around data visualisation, experimental interfaces and just good old fashioned graphic design!</p>\n<p>I’ll have some availability in May, and am wide-open from June onwards; <a href=\"mailto:hire@designedbygold.com\">say hi and let’s make things happen</a>. I’m not looking to jump back into a full-time job immediately, but would love to hear from you if you think I’d be a good fit for your company anyway.</p>\n<p>Rad, I think that’s all. Sad to be leaving Makeshift, but they’re in better shape than ever with some great new additions to the team and new features coming out soon across all the products. Mainly, I’m excited by the future and new possibilities. Take it away, Coverdale.</p>\n<iframe width=\"480\" height=\"360\" src=\"//www.youtube.com/embed/WyF8RHM1OCg?rel=0\" frameborder=\"0\" allowfullscreen></iframe>\n"}

