	module.exports = {"layout":"post","published":true,"date":"2014-07-08T00:00:00.000Z","title":"The Death of the CMS","body":"<p>…has been greatly exaggerated.</p>\n<p>I’m going to try a new thing when I blog any comment I make on {Designer,Hacker}News over a sentence or two. We’ll see how it goes. Posterity and all that.</p>\n<p><a href=\"https://news.layervault.com/stories/27897-static-vs-cms-whats-the-best-solution-for-clients\">Tonight on DesignerNews</a></p>\n<blockquote>\n<p>There was a post earlier today about whether or not people still build static sites for their clients. That discussion begs the question, if not static then what?\nThere’s a wide range of ‘CMS’ options out there from database-driven solutions like WordPress and Drupal to static site generators like Jekyll and Middleman. WordPress and Drupal can be easy for clients to update once they are set up, but there is a ton of associated overhead. Jekyll and Middleman are dead simple, but require some knowledge of command line and markdown to redeploy after making changes.\nWhen dealing with clients building stuff like landing and marketing pages what is your preferred solution? What’s the perfect balance between usability for the client and ease of development?</p>\n</blockquote>\n<p>A better CMS, I think. I really don’t know the answer, but I want to kill myself working with WordPress or Drupal - so whilst the client might enjoy the editing interface, they won’t be getting a website to edit because they won’t have a web designer by that point.</p>\n<p>Jekyll is fantastic but obviously it’s not perfect for non-technical users. Things like <a href=\"http://Prose.io\">Prose.io</a> are cool but they still don’t tell the whole story - they’re great for editing <em>text</em> but CMS’s aren’t just about editing text - they’re about reviewing drafts and installing plugins and doing <em>adminy stuff</em> - letting people feel like they’re in control.</p>\n<p>I don’t think Jekyll is great because it’s a static site generator - it’s great because it has a sane API, where the alternatives have mindnumbingly awful APIs. I’d keep an eye on Ghost and Buckets - I don’t think the CMS is dead, just that the incumbents are shitshows.</p>\n<p>The caveat is the death of CMSs for small, personal sites for technical owners. Remember years ago when every designers’ portfolio site was a sprawling WordPress/Drupal/Joomla/PHP Nuke/Mambo/kill-me-now mess? Every post was a database entry in a MySQL database you had no idea how anything really worked and FOR THE LOVE OF GOD ALL YOU WANTED TO DO WAS LOOP OVER SOME IMAGES? Jekyll solves that; there’s no need for a CMS for that junk.</p>\n<hr>\n<p>(He blogs, from Jekyll via <a href=\"http://Prose.io\">Prose.io</a>)</p>\n"}

