	module.exports = {"layout":"post","published":true,"date":"2015-05-13T00:00:00.000Z","title":"Framer.js for PC & Linux users","body":"<p>One of the most common questions I see in the <a href=\"https://www.facebook.com/groups/framerjs/\">Framer Facebook group</a> is people wondering whether you need to use a Mac to run Framer. That’s understandable — the website has a screenshot of a Mac app, a download button, and a caption saying ‘available for Mac’. Here’s the thing — you can use Framer and join this wonderful community &amp; way of working on <em>any platform</em>. Let me explain…</p>\n<p>The app you see in the screenshots on the <a href=\"http://framerjs.com/\">Framer website</a> is ‘Framer Studio’. It’s a paid app, for OSX only. The thing that powers it though—Framer.js—is open source, and available for free on GitHub.</p>\n<p>To save you having to compile the source, Framer provide a simple <a href=\"http://framerjs.com/static/downloads/Framer.zip\">.zip file</a> to get you started - including all of the JavaScript you need, and an HTML file to open in your browser. It’s that simple to get started with Framer on Windows or Linux!</p>\n<h2>So what’s Framer Studio? Where’s that lovely UI?</h2>\n<p>Framer Studio is a collection of tools around Framer.js itself. Some of these are unique, some of them you can replicate yourself on <em>any computer</em>. It won’t be as smooth as using Framer Studio on a Mac, but certainly easier than making a trip to the Apple Store.</p>\n<p>Some of the notable features:</p>\n<ul>\n<li>CoffeeScript compilation</li>\n<li>LiveReload</li>\n</ul>\n<p>More on those in a second. Some features that you can get an approximation of in  any editor (e.g. <a href=\"http://www.sublimetext.com/\">Sublime Text</a>):</p>\n<ul>\n<li>Syntax highlighting</li>\n<li>Snippets</li>\n<li><a href=\"https://github.com/awt2542/SublimeCompletionFramerjs\">Autocomplete</a></li>\n</ul>\n<p>And then some more nifty things that are unique to the Mac app:</p>\n<ul>\n<li>Templates</li>\n<li>Sharing</li>\n<li>Presentation view</li>\n<li>Mirror</li>\n<li>Zoom</li>\n<li>Sketch/Photoshop importing</li>\n</ul>\n<p>Framer Studio is definitely <strong>optional</strong>, but the features you get are <strong>really nice</strong>.</p>\n<p>With that said: CoffeeScript!</p>\n<h2>Why does my code break when I try to run the examples from the website?</h2>\n<p><a href=\"http://framerjs.com/static/downloads/Framer.zip\">That link</a> you just download is in <strong>JavaScript</strong>. Framer Studio, and all of the examples on the website, are in <strong>CoffeeScript</strong>.</p>\n<p><a href=\"http://coffeescript.org/\">CoffeeScript</a> is a version of JavaScript with a bunch of nifty features, but most importantly for us, it gets rid of a bunch of brackets. JavaScript <strong>needs_</strong> those brackets.</p>\n<p>For example, the first page of <a href=\"http://framerjs.com/learn/basics/\">Framer Basics</a> shows you how to create a simple layer.</p>\n<pre><code>layerA = <span class=\"hljs-keyword\">new</span> Layer\n<span class=\"hljs-symbol\">  x:</span> <span class=\"hljs-number\">0</span>, <span class=\"hljs-string\">y:</span> <span class=\"hljs-number\">0</span>, <span class=\"hljs-string\">width:</span> <span class=\"hljs-number\">100</span>, <span class=\"hljs-string\">height:</span> <span class=\"hljs-number\">100</span>\n</code></pre>\n<p>If you try to add that to the <code>app.js</code> file you downloaded, you’ll be confronted with some nasty errors. In JavaScript it should look more like this:</p>\n<pre><code>var layerA = new Layer({\n<span class=\"hljs-symbol\">  x:</span> <span class=\"hljs-number\">0</span>,\n<span class=\"hljs-symbol\">  y:</span> <span class=\"hljs-number\">0</span>,\n<span class=\"hljs-symbol\">  width:</span> <span class=\"hljs-number\">100</span>,\n<span class=\"hljs-symbol\">  height:</span> <span class=\"hljs-number\">100</span>\n});\n</code></pre>\n<p>Not too different, but enough to make your web browser complain! Let’s look at a more complex example and then I’ll show you how to fix it :)</p>\n<pre><code>layerA<span class=\"hljs-selector-class\">.animate</span> \n  properties:\n    x:<span class=\"hljs-number\">200</span>\n  curve: <span class=\"hljs-string\">\"ease-in-out\"</span>\n\nlayerA<span class=\"hljs-selector-class\">.on</span> Events<span class=\"hljs-selector-class\">.AnimationEnd</span>, -&gt;\n  layerA<span class=\"hljs-selector-class\">.animate</span>\n    properties:\n      x:<span class=\"hljs-number\">100</span>\n    curve: <span class=\"hljs-string\">\"ease-in-out\"</span>\n</code></pre>\n<p>In JavaScript that would be more like</p>\n<pre><code><span class=\"hljs-selector-tag\">layerA</span><span class=\"hljs-selector-class\">.animate</span>({\n  <span class=\"hljs-attribute\">properties</span>: {\n    <span class=\"hljs-attribute\">x</span>: <span class=\"hljs-number\">200</span>\n  },\n  <span class=\"hljs-attribute\">curve</span>: <span class=\"hljs-string\">\"ease-in-out\"</span>\n});\n\n<span class=\"hljs-selector-tag\">layerA</span><span class=\"hljs-selector-class\">.on</span>(Events.AnimationEnd, function() {\n  <span class=\"hljs-selector-tag\">layerA</span><span class=\"hljs-selector-class\">.animate</span>({\n    <span class=\"hljs-attribute\">properties</span>: {\n      <span class=\"hljs-attribute\">x</span>: <span class=\"hljs-number\">100</span>\n    },\n    <span class=\"hljs-attribute\">curve</span>: <span class=\"hljs-string\">\"ease-in-out\"</span>\n  });\n});\n</code></pre>\n<p>At this point, there’s a few things you could do.</p>\n<ul>\n<li>You could translate every line of CoffeeScript you read on the Framer website &amp; in other peoples’ code into JavaScript in your head.</li>\n<li>You could use a website like <a href=\"http://js2.coffee/\">JS2Coffee</a> and get it to translate itself, then copy &amp; paste that into your project.</li>\n<li>Or, you can just set up CoffeeScript on your PC.</li>\n</ul>\n<p>I really recommend the third option. If you’re a relative newcomer to JavaScript, you don’t need the added hassle of hoping your code matches up to the code you’re reading in a tutorial. It’s much nicer to use the same language as the rest of the community.</p>\n<p>Luckily, it’s pretty easy to get it all wired up.</p>\n<h2>The easy way</h2>\n<p>Whilst there’s nothing like Framer Studio for Windows or Linux, there are some other nice apps that can help us out.</p>\n<p><a href=\"https://prepros.io/\">Prepros</a> is available for Windows, Linux &amp; Mac, and helps you compile CoffeeScript (as well as Sass, LESS, Jade &amp; others — you can use this for lots of projects besides Framer). To get started, install &amp; open the app, then add the <code>Project</code> folder from the .zip you downloaded earlier.</p>\n<p>Next, rename <code>app.js</code> to <code>app.coffee</code>, and replace its contents with some CoffeeScript. Here’s what it converts to:</p>\n<pre><code>iconLayer = new Layer width:<span class=\"hljs-number\">256</span>, height:<span class=\"hljs-number\">256</span>, image:<span class=\"hljs-string\">\"images/icon.png\"</span>\niconLayer.center()\n\n# Define a <span class=\"hljs-keyword\">set</span> of <span class=\"hljs-comment\">states with names (the original state is</span> <span class=\"hljs-comment\">'default'</span><span class=\"hljs-comment\">)</span>\niconLayer.states.add\n  second: {y:100, scale:0.6, rotationZ:100}\n  third:  {y:300, scale:1.3, blur:4}\n  fourth: {y:200, scale:0.9, blur:2, rotationZ:200}\n\n# <span class=\"hljs-keyword\">Set</span> <span class=\"hljs-comment\">the default animation options</span>\niconLayer.states.animationOptions <span class=\"hljs-comment\">=</span>\n  curve: <span class=\"hljs-string\">\"spring(500,12,0)\"</span>\n\n# On <span class=\"hljs-comment\">a click, go to the next state</span>\niconLayer.on <span class=\"hljs-comment\">Events.Click, -&gt;</span>\n  iconLayer.states.next()\n</code></pre>\n<p>Then…</p>\n<ul>\n<li>Drag your folder into Prepros.</li>\n<li>Click on <code>app.coffee</code></li>\n<li>Make sure ‘auto compile’ is checked</li>\n<li>Click ‘process file’</li>\n<li>Click ‘Live preview’ in the toolbar</li>\n</ul>\n<p>Your browser should open, and your prototype should be running. From there, change anything you want to in <code>app.coffee</code> and your browser should magically reload.</p>\n<p>There are some other options for compiling Coffeescript on Windows - feel free to use another GUI if that’s what you do already, but Prepress is pretty great for an open source tool.</p>\n<p>You could also go for more fine-grained control by compiling your Coffeescript etc yourself with something like <code>gulp</code> or <code>grunt</code>, but remember, the goal is to be prototyping as quickly as possible, not getting bogged down in tool configuration :)</p>\n"}

