	module.exports = {"layout":"post","published":true,"date":"2015-07-01T00:00:00.000Z","title":"Functional Programming, CSS, and your sanity","body":"<p>Learning Clojure and/or Haskell will make you a better programmer. Even if you don’t use it day to day, there are a ton of prescient lessons to be taken back to your language of choice.</p>\n<p>Functional programming is infectious. I find it beautiful &amp; elegant in much the same way as a <a href=\"http://dieterrams.tumblr.com/\">Braun watch</a> or Karl Gerstner’s grid system for <a href=\"https://www.flickr.com/photos/watz/2382027037/sizes/l/\">Capital magazine</a>. Good design (my preferred school of good design, at least) is mathsy, rational and pure—and CSS is design—so it follows that there are a bunch of lessons we can bring back from FP land into design.</p>\n<p>I’ve been meaning to write this post for months but technical blogging takes me ages — excuse the brain dump.</p>\n<p>These ideas have been swirling around for a few years; shout out to <a href=\"https://twitter.com/jxnblk\">Brent Jackson</a> and <a href=\"https://twitter.com/mrmrs_\">Adam Morse</a> for shipping something incredible in <a href=\"http://www.basscss.com/\">Basscss</a> &amp; <a href=\"http://tachyons.io/\">Tachyons</a> respectively, whilst I’ve been sitting and thinking about it.</p>\n<h1>f(css) is:</h1>\n<ul>\n<li><strong>Functional</strong>. Small, clear, easy to read classes that are easy to apply and do one thing. You might have called them ‘utility classes’ with some snark, but they rock. A random example from Basscss would be the margin &amp; padding classes - <code>mb1</code>, <code>px2</code> (margin-bottom-1, padding-x-2) etc. The super short naming conventions are optional (I just love Brent’s implementation) — I guess you could write a library with less terse class names if you’re a masochist, but FP seems to tend toward short variable names. When you consolidate all of your sizing &amp; spacing into reusable classes you <em>force</em> a beautiful type scale &amp; rhythm on your design. Neat!</li>\n<li><strong>Composable</strong>. In FP it’s fun to compose a bunch of small functions together to create larger actions. The parallel in f(css) is building up big ol’ chains of tiny classes. Think declarative composition — <code>&lt;a class=&quot;mb2 bg-green white px2 rounded&quot;&gt;</code> rather than OO-derived objects &amp; modifiers — <code>&lt;a class=&quot;btn btn--primary&quot;&gt;</code>. Scared of writing lots of classes? No shit, everyone hates typing. Wrap them up in React components to your heart’s content. <a href=\"https://github.com/JedWatson/classnames\">classnames</a> is a fun module for dynamically composing groups of classes, so you don’t end up with massive strings all over the shop. <strong>The point isn’t to get away from component-based <em>design</em></strong> (which rocks and should be a cornerstone of your design ethos); rather you should get away from component-based CSS, which is crappy and repetitive.</li>\n<li><strong>Immutable</strong>. On to the fun stuff. Declare a property once and you should be damn sure it’s never going to get overwritten. Fuck specificity. Apply the class you want — margin-bottom, say — and rest assured that no one else’s CSS is going to override it. The problem here is that CSS isn’t immutable (has anyone made an actually-immutable CSS preprocessor? If not I’m going to write one), but be clever in your implementation and it’s <em>kind of like immutability</em>.</li>\n<li><strong>Referentially transparent</strong>. This is a fancy way of saying ‘it should be guaranteed to do the thing you want it to do’. The cascade and ems are two of the worst things about CSS. I’m tired of playing guesswork because some framework author decided that using multipliers was a good idea.</li>\n<li><strong>Side-effect free</strong>. not only should a class do the same thing every time, but it should never, ever change anything other than what you’re targeting.</li>\n</ul>\n<p>I’m aware that this is almost the polar opposite of both OOCSS/SMACSS, and everything that some semantic web bore told you off for doing once upon a time, but I think this is the future of CSS. It’s easy to reason about, quick to write, lightning fast for browsers to parse, and gets rid of the clusterfuck that is the ‘C’ in CSS.</p>\n<p>Tweet me if you massively disagree, I’ve fudged an FP metaphor, you’ve built an immutable CSS processor, or you think CSS class names should be machine-readable (lol).</p>\n<p>edit: <a href=\"https://twitter.com/4lpine\">John Otander</a> is working on a linter for immutable CSS. This makes me happy. <a href=\"https://github.com/johnotander/immutable-css\">johnotander/immutable-css</a></p>\n<p>Further reading:</p>\n<ul>\n<li><a href=\"http://csswizardry.com/2015/03/immutable-css/\">Immutable CSS</a> by Harry Roberts</li>\n<li><a href=\"http://eng.wealthfront.com/2013/08/functional-css-fcss.html\">Functional CSS</a> by Wealthfront</li>\n</ul>\n<p>Thanks to <a href=\"https://twitter.com/paulozoom\">Paulo Pereira</a>, <a href=\"https://twitter.com/colinstrickland\">cms</a>, <a href=\"https://twitter.com/theefer\">Sébastian Cevey</a>, <a href=\"https://twitter.com/jxnblk\">Brent Jackson</a> for feedback on this post :)</p>\n"}

